	.btn {
		border-radius: 2px;
		transition: all .3s ease-in-out;
	}
	.btn-dark {
		color: #fff;
		background-color: #444;
		border-bottom: 2px solid #bbff66;
	}
	.btn-dark:hover,
	.btn-dark:focus,
	.btn-dark:active {
		color: #fff;
		background-color: #000;
		transition: all .3s ease-in-out;
	}
	.btn-light {
		color: #333;
		background-color: #fff;
		border-bottom: 2px solid #bbff66;
	}
	.btn-light:hover,
	.btn-light:focus,
	.btn-light:active {
		color: #333;
		background-color: #ddd;
		transition: all .3s ease-in-out;
	}

	/* Social icons */
	.btn-social {
		display: inline-block;
		width: 50px;
		height: 50px;
		border: 2px solid #bbff66;
		border-radius: 100%;
		text-align: center;
		font-size: 20px;
		line-height: 45px;
		color: #fff;
		background-color: #777;
		-webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
		box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
	}
	.btn-outline {
		margin-top: 15px;
		border: solid 2px #bbff66;
		font-size: 20px;
		color: #bbff66;
		background: 0 0;
		transition: all .3s ease-in-out;
	}
	.btn-outline:hover, .btn-outline:focus, .btn-outline:active, .btn-outline.active {
	  border: solid 2px #bbff66;
	  color: #bbff66;
	  background: #000;
	  transition: all .3s ease-in-out;
	}
	.callout .btn-social {
		-webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
		box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
		color: #fff;
		background-color: #444;
		border-color: #bbff66;
	}
	.callout .btn-social:hover {
		background-color: #000;
		border-color: #bbff66;
	}

	.img-thumbnail {
		border: none;
		background-color: #bbff66;
		box-shadow: 0 5px 10px rgba(0,0,0,.2);
	}
	.img-center {
		margin-left: auto;
		margin-right: auto;
		display: block;
	}

	.text-vertical-center {
		display: table-cell;
		text-align: center;
		vertical-align: middle;
	}
	.text-vertical-center h1{
		font-size: 4em;
		font-weight: 300;
	}
	.text-vertical-center h2{
		font-size: 3em;
		font-weight: 300;
	}
		.parallax-section .text-vertical-center,
		.callout .text-vertical-center {
			background-color: rgba(0,0,0,.2);
		}
		.callout .text-vertical-center {
			position: absolute;
			width: 100%;
			height: 100%;
			z-index: 2;
		}

	hr.small {
		max-width: 100px;
	}

	.content {
		/*margin-top: 65px;*/
	}

	.main {
		padding-left: 0;
		padding-right: 0;
	}
	.container-fluid {
		padding-right: 0;
		padding-left: 0;
	}
	.row {
		margin-left: 0;
		margin-right: 0;
	}

	.navbar {
		margin-bottom: 0;
	}
		.navbar-default {
			background-color: transparent;
		}
		.navbar-custom {
			height: 72px;

			-webkit-transition: background .5s ease-in-out,padding .5s ease-in-out;
			-moz-transition: background .5s ease-in-out,padding .5s ease-in-out;
			transition: background .5s ease-in-out,padding .5s ease-in-out;
		}

		body.dashboard-concept .navbar-custom {
			display: none;
		}
			.navbar-toggle {
				color: #ffffff;
				text-shadow: none;
				padding: 4px 8px;
				border-radius: 0;
				margin-top: 5px;
			}
			.nav>li>a {
				transition: all .3s ease-in-out;
				color: #fff;
			}
			.nav>li>a:focus, .nav>li>a:hover {
				background: transparent;
			}


		.navbar-custom.top-nav-collapse {
			padding: 0;
			border-bottom: 1px solid rgba(255,255,255,.3);
			background: #000;
		}       
			.navbar-brand {
				height: 65px;
				padding: 5px;

				-webkit-transition: 0.5s;
				-moz-transition: 0.5s;
				-ms-transition: 0.5s;
				transition: 0.5s;
			}
				.navbar-brand img {
					height: 100%;
				}
		.shrink {
			background: #333;
			border-bottom: 2px solid #66BBFF;
			height: 54px;
			-webkit-transition: 0.5s;
			-moz-transition: 0.5s;
			-ms-transition: 0.5s;
			transition: 0.5s;
		}
		.shrink .navbar-custom {
			padding-top: 0px;
			-webkit-transition: 0.5s;
			-moz-transition: 0.5s;
			-ms-transition: 0.5s;
			transition: 0.5s;
		}
		.shrink .navbar-toggle {
			margin-top: 3px;
		}
		.shrink .navbar-brand {
			height: 50px;
			-webkit-transition: 0.5s;
			-moz-transition: 0.5s;
			-ms-transition: 0.5s;
			transition: 0.5s;
		}
		.shrink .nav>li>a:focus,
		.shrink .nav>li>a:hover,
		.shrink .nav>li.active {
			background-color: #66BBFF;
			 -webkit-transition: 0.5s;
				-moz-transition: 0.5s;
				-ms-transition: 0.5s;
				transition: 0.5s;
		}

	.parallax-section {
		width: 100%;
		height: 500px;
		display: table;
		position: relative;
	}
	.parallax-section h1, .parallax-section h2, 
		.parallax-section h3, .parallax-section h4, 
		.parallax-section p, .parallax-section hr, 
		.parallax-section blockquote, .parallax-section i {
			text-shadow: 1px 1px 2px #444;
			font-weight: 300;
			opacity: 1;
			color: #fff;
		}

	.modal-body .btn-social {
		border-color: #000;
		color: #000; 
	}

	.modal-body .btn-social:hover {
		border-color: #333;
		color: #333;
	}

	/* Callout */
	.callout {
		display: table;
		position: relative;
		width: 100%;
		height: 100vh;
		color: #fff;

		-webkit-background-size: cover;
		-moz-background-size: cover;
		background-size: cover;
		-o-background-size: cover;
	} 
	  .callout h2 {
		margin-top: 15%;
	  }
		.callout h1, .callout h2, 
		.callout h3, .callout h4, 
		.callout p, .callout hr {
			text-shadow: 1px 1px 2px #444;
			font-weight: 300;
			opacity: 1;
		}

	/* Add padding to sections */
	.about, .call-to-action, .portfolio, .services {
		padding: 50px 0;
	}
	.portfolio {
		padding-bottom: 0;
	}
	.about {
		color: #fff;
	}

	/* Services */
	.service-item {
		margin-bottom: 20px;
	}

	/* Portfolio */
	#portfolio .portfolio-item {
		min-height: 250px;
		right: 0;
		margin: 0;
		padding: 0;
	}
	#portfolio .portfolio-item .portfolio-link {
		display: block;
		position: relative;
		margin: 0 auto;
		width: 100%;
		height: 250px;
	}
	#portfolio .portfolio-item .portfolio-link img {
	  max-height: 175px;
	  padding: 55px;
	}
	#portfolio .portfolio-item .portfolio-link .caption {
		position: absolute;
		width: 100%;
		height: 100%;
		opacity: 0;
		background: rgba(68,68,68,.75);

		-webkit-transition: all ease .5s;
		-moz-transition: all ease .5s;
		transition: all ease .5s;
	}
	#portfolio .portfolio-item .portfolio-link .caption:hover {
		opacity: 1;
	}
	#portfolio .portfolio-item .portfolio-link .caption .caption-content {
		position: absolute;
		top: 55%;
		width: 100%;
		text-align: center;
		font-size: 20px;
		color: #fff;
	}

	#portfolio .portfolio-item .portfolio-link .caption .caption-content i {
		margin-top: -12px;
	}

	#portfolio .portfolio-item .portfolio-link .caption .caption-content h3,
	#portfolio .portfolio-item .portfolio-link .caption .caption-content h4 {
		margin: 0;
	}

	#portfolio * {
		z-index: 2;
	}
	.portfolio-modal .modal-content {
		text-align: center;
	}
	.modal-content {
		padding: 100px 0;
		min-height: 100%;
		border: 0;
		border-radius: 0;
		background-clip: border-box;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	.modal-content h2 {
		margin: 0;
		font-size: 1.7em;
	}
	.modal-content img {
		margin-bottom: 30px;
	}
	.modal-content .item-details {
		margin: 30px 0;
	}
	.close-modal {
		position: absolute;
		top: 25px;
		right: 25px;
		width: 75px;
		height: 75px;
		background-color: transparent;
		cursor: pointer;
	}
	.close-modal:hover {
		opacity: .3;
	}
	.modal-backdrop {
		display: none;
		opacity: 0;
	}

	.broke-2015 .panel {
