	.broke-2015 .portfolio-item {
		margin-bottom: 30px;
	}

	.call-to-action {

	}
	.copyright-2015 {
		border-top: 2px solid #66BBFF;
	}
	.copyright-2015 .panel {
		border-radius: 0px;
		border: none;
		margin-bottom: 0;
	}
		.copyright-2015 .panel-body {
			color: #fff;
			border-radius: 1px;
			border: none;
		}

	/* Media Fixes for theme */
	@media(max-width: 768px) {
		.bg-cameron-falling,
		.bg-cameron-falling-sideways,
		.bg-cameron-diving,
		.bg-cameron-pull-chord,
		.bg-cameron-pull-chord-close,
		.bg-cameron-parachuting,
		.bg-cameron-2015,
		.bg-misty-mt,
		.bg-misty-trees,
		.bg-filter-creek,
		.bg-filter-diner,
		.bg-foggy-river,
		.bg-desk-lamp,
		.bg-drawing-man {
			background-image: none;
			background-color: #444;
		}
		.navbar .nav {
			margin: 0px -15px 0;
			background-color: rgba(0,0,0,.6);
		}
	}

